  width: 14px;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  border-radius: 3px;
  background-color: #E8E8E8; }

.checkbox + label:after {
  content: '';
  display: block;
  height: 4px;
  width: 6px;
  border-left: 2px solid #494949;
  border-bottom: 2px solid #494949;
  position: absolute;
  left: 3px;
  top: 3px;
  z-index: 2;
  transform: rotate(-45deg);
  opacity: 0; }

.checkbox:checked + label:after {
  opacity: 1; }

